import React, { memo } from 'react';
import styled from 'styled-components';
import Button from '../styled/Button';
import personIcon from '../../images/person-icon.svg';
import { viewports } from '../../utils/variables';

const ImageUploadInput = ({ name, uploadCta }) => (
  <Base>
    <InputLabel htmlFor={name} />
    <Input type="file" name="image" accept="image/*" />
    <PhotoButtonContainer>
      <UploadPhotoButton>{uploadCta}</UploadPhotoButton>
      <DeletePhotoButton secondary>Delete Photo</DeletePhotoButton>
    </PhotoButtonContainer>
  </Base>
);

export default memo(ImageUploadInput);

const Base = styled.div`
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.875rem;
  position: relative;

  @media ${viewports.medium} {
    justify-content: flex-start;
  }
`;

const InputLabel = styled.div`
  background: url(${personIcon});
  background-position: 100%;
  background-size: cover;
  border-radius: 50%;
  cursor: pointer;
  height: 7.5rem;
  margin-top: 0.375rem;
  position: relative;
  width: 7.5rem;

  @media ${viewports.medium} {
    &:hover {
      &:after {
        background: rgba(34, 34, 34, 0.1);
        bottom: 0;
        border-radius: 50%;
        content: '';
        display: block;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
      }
    }
  }
`;

const Input = styled.input`
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
`;

const PhotoButtonContainer = styled.div`
  margin-left: 1.875rem;
`;

const UploadPhotoButton = styled(Button)`
  margin: 0 0 0.625rem;

  @media ${viewports.medium} {
    width: 12.5rem;
  }
`;

const DeletePhotoButton = styled(UploadPhotoButton)`
  margin: 0;
`;
