import React, { memo, useEffect, useRef } from 'react';
import { disableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';
import gql from 'graphql-tag';
import { Form } from 'formik';
import styled from 'styled-components';
import { Mutation } from 'react-apollo';
import Overlay from '../styled/Overlay';
import MultiPageForm, { MultiPageFormContext } from '../shared/MultiPageForm';
import Welcome from './Welcome';
import Career from './Career';
import Social from './Social';
import Bio from './Bio';
import Finish from './Finish';
import { careerValidation } from '../../utils/validationSchema';
import { viewports } from '../../utils/variables';

const ProfileSetup = ({ setActiveScreen, id, refetch }) => {
  const overlayRef = useRef(null);

  useEffect(() => {
    disableBodyScroll(overlayRef.current);
    return () => clearAllBodyScrollLocks();
  });

  return (
    <Overlay ref={overlayRef}>
      <Mutation mutation={PROFILE_SETUP}>
        {profileSetup => (
          <MultiPageForm
            screens={[
              <Welcome />,
              <Career validate={careerValidation} />,
              <Social />,
              <Bio />,
              <Finish setActiveScreen={setActiveScreen} refetch={refetch} />,
            ]}
            initialValues={{
              currentCompany: '',
              currentJobTitle: '',
              currentIndustry: null,
              website: '',
              linkedIn: '',
              instagram: '',
              facebook: '',
              twitter: '',
              profileImage: '',
              bio: '',
              visibleFields: {
                email: true,
                currentCompany: true,
                currentJobTitle: true,
                currentIndustry: true,
                website: false,
                linkedIn: false,
                instagram: false,
                facebook: false,
                profileImage: false,
                bio: false,
              },
            }}
            submit={async values => {
              const { data } = await profileSetup({
                variables: { ...values, id: +id },
              });
              return data;
            }}
          >
            <MultiPageFormContext.Consumer>
              {({ activeScreen }) => <Base>{activeScreen}</Base>}
            </MultiPageFormContext.Consumer>
          </MultiPageForm>
        )}
      </Mutation>
    </Overlay>
  );
};

export default memo(ProfileSetup);

const Base = styled(Form)`
  background-color: #fff;
  bottom: 0;
  box-shadow: 8px 8px 20px 0 rgba(34, 34, 34, 0.06);
  display: flex;
  left: 0;
  overflow-y: auto;
  padding: 2.75rem 1.875rem;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;

  @media ${viewports.medium} {
    display: block;
    overflow-y: visible;
    padding: 0 3.125rem;
    position: relative;
    width: 40rem;
  }
`;

const PROFILE_SETUP = gql`
  mutation profileSetup(
    $id: Int!
    $currentCompany: String
    $currentJobTitle: String
    $currentIndustry: SelectInput
    $website: String
    $linkedIn: String
    $instagram: String
    $facebook: String
    $twitter: String
    $profileImage: String
    $bio: String
    $visibleFields: VisibleFieldsInput
  ) {
    profileSetup(
      id: $id
      currentCompany: $currentCompany
      currentJobTitle: $currentJobTitle
      currentIndustry: $currentIndustry
      website: $website
      linkedIn: $linkedIn
      instagram: $instagram
      facebook: $facebook
      twitter: $twitter
      profileImage: $profileImage
      bio: $bio
      visibleFields: $visibleFields
    ) {
      id
      email
    }
  }
`;
