import React, { memo } from 'react';
import styled from 'styled-components';
import { Label } from '../styled/Typography';
import Input from '../styled/Input';
import Toggle from '../shared/Toggle';
import { viewports } from '../../utils/variables';

const TextAreaInput = ({
  label,
  name,
  placeholder,
  value,
  totalCharacters,
  toggle,
  toggled,
  form,
  touched,
  error,
  onChange,
}) => (
  <Base>
    <InputContainer>
      {label && <Label htmlFor={name}>{label}</Label>}
      <NumCharacters>
        {value.length} / {totalCharacters} characters
      </NumCharacters>
      <TextArea
        as="textarea"
        name={name}
        rows="8"
        value={value}
        placeholder={placeholder}
        error={touched && error}
        maxLength={totalCharacters}
        onChange={onChange}
      />
    </InputContainer>
    {toggle && <TextAreaToggle form={form} toggled={toggled} name={name} />}
  </Base>
);

export default memo(TextAreaInput);

const Base = styled.div`
  align-items: flex-start;
  margin-bottom: 1.875rem;

  @media ${viewports.medium} {
    display: flex;
  }
`;

const InputContainer = styled.div`
  position: relative;
  width: 100%;
`;

const NumCharacters = styled.span`
  color: #ccc;
  font-size: 0.75rem;
  position: absolute;
  right: 0;
  top: 0;
`;

const TextArea = styled(Input)`
  height: 12.5rem;
  padding: 0.9375rem;
`;

const TextAreaToggle = styled(Toggle)`
  @media ${viewports.medium} {
    margin-top: 21px;
  }
`;
