import React, { memo, Fragment, useContext } from 'react';
import styled from 'styled-components';
import { Spring, animated } from 'react-spring';
import { fontFamilies, colors } from '../../utils/variables';
import { MultiPageFormContext } from './MultiPageForm';

const ProgressNav = ({ numScreens, firstIndex }) => {
  const { formScreen, flow } = useContext(MultiPageFormContext);

  return (
    formScreen < numScreens + firstIndex && (
      <Base>
        <Nav>
          {numScreens &&
            Array.from(Array(numScreens).keys()).map(screenIndex => {
              const active = formScreen === screenIndex + firstIndex;
              const completed = formScreen > screenIndex;
              const dividerCompleted =
                flow === 'forward'
                  ? formScreen > screenIndex + firstIndex
                  : formScreen > screenIndex - 1 + firstIndex;
              return (
                <Fragment key={screenIndex}>
                  <IndicatorContainer>
                    <Spring
                      native
                      from={{ o: 0, backgroundColor: '#ddd' }}
                      to={{
                        o: active ? 1 : 0,
                        background: completed || active ? colors.red : '#ddd',
                      }}
                    >
                      {({ o, background }) => (
                        <>
                          <animated.div
                            style={{
                              opacity: o.interpolate(o => o),
                            }}
                          >
                            <ActiveIndicator />
                          </animated.div>
                          <animated.div
                            style={{
                              background,
                              alignItems: 'center',
                              borderRadius: '50%',
                              color: '#fff',
                              display: 'flex',
                              fontFamily: fontFamilies.bold,
                              fontSize: '0.75rem',
                              height: '1.5rem',
                              justifyContent: 'center',
                              position: 'relative',
                              width: '1.5rem',
                            }}
                          >
                            {screenIndex + 1}
                          </animated.div>
                        </>
                      )}
                    </Spring>
                  </IndicatorContainer>
                  {screenIndex !== numScreens - 1 && (
                    <DividerContainer>
                      <Divider completed={completed} />
                      <Spring
                        native
                        key={() => screenIndex}
                        from={{ x: -100 }}
                        to={{
                          x: dividerCompleted ? 0 : -100,
                        }}
                        immediate={
                          flow === 'forward'
                            ? screenIndex === formScreen - 2 - firstIndex
                            : screenIndex === formScreen - 1 - firstIndex
                        }
                        reverse={
                          flow === 'backward' &&
                          screenIndex === formScreen - firstIndex
                        }
                      >
                        {({ x }) => (
                          <animated.div
                            style={{
                              transform: x.interpolate(
                                x => `translateX(${x}%)`
                              ),
                              backgroundColor: colors.red,
                              bottom: 0,
                              left: 0,
                              right: 0,
                              position: 'absolute',
                              top: 0,
                            }}
                          />
                        )}
                      </Spring>
                    </DividerContainer>
                  )}
                </Fragment>
              );
            })}
        </Nav>
      </Base>
    )
  );
};

export default memo(ProgressNav);

const Base = styled.div`
  margin-bottom: 2.1875rem;
  text-align: center;
`;

const Nav = styled.nav`
  align-items: center;
  display: inline-flex;
  justify-content: center;
  position: relative;
  z-index: 0;
`;

const IndicatorContainer = styled.div`
  position: relative;
  z-index: 0;
`;

const ActiveIndicator = styled.div`
  background-color: rgba(252, 70, 0, 0.2);
  border-radius: 50%;
  height: 2.125rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 2.125rem;
  z-index: -1;
`;

const DividerContainer = styled.div`
  overflow: hidden;
  position: relative;
  z-index: -1;
`;

const Divider = styled.div`
  background-color: #eee;
  height: 0.25rem;
  position: relative;
  width: 3.125rem;
`;
