import React, { memo } from 'react';
import { Field } from 'formik';
import Pagination from '../shared/Pagination';
import TextArea from '../inputs/TextAreaInput';
import ImageUploadInput from '../inputs/ImageUploadInput';
import Wrapper from './Wrapper';

const Career = () => (
  <Wrapper
    isLast
    currentScreen={2}
    skipScreenText="Skip & Finish"
    skipScreenRoute="/explore/profile-setup/finish"
    heading="Photo & Bio"
    subheading="Link to your website or social media to broaden your connections."
  >
    <Field name="profileImage">
      {({ field }) => (
        <ImageUploadInput {...field} label="Photo" uploadCta="Upload Photo" />
      )}
    </Field>
    <Field name="bio">
      {({ field, form }) => (
        <TextArea
          {...field}
          toggle
          toggled={form.values.visibleFields.bio}
          form={form}
          label="Short Bio"
          totalCharacters={350}
          placeholder="Tell people about yourself."
          error={form.errors.bio}
          touched={form.touched.bio}
        />
      )}
    </Field>
    <Pagination centered prevActive submitActive submitText="Finish" />
  </Wrapper>
);

export default memo(Career);
