import React, { memo, createContext, useState } from 'react';
import { Formik } from 'formik';

export const MultiPageFormContext = createContext();

const MultiPageForm = ({ screens, children, initialValues, submit }) => {
  const [formScreen, setFormScreen] = useState(0);
  const [flow, setFlow] = useState('forward');

  const handleNextScreen = () => {
    setFormScreen(formScreen + 1);
    setFlow('forward');
  };

  const handlePrevScreen = () => {
    setFormScreen(formScreen - 1);
    setFlow('backward');
  };

  const validate = values => {
    const activeScreen = screens[formScreen];
    return activeScreen.props.validate
      ? activeScreen.props.validate(values)
      : {};
  };

  const handleSubmit = async (values, actions) => {
    const isLastPage = screens.length - 2 === formScreen;
    if (isLastPage) {
      try {
        await submit(values);
        actions.setSubmitting(false);
        handleNextScreen();
      } catch (err) {
        actions.setSubmitting(false);
      }
    } else {
      handleNextScreen();
      actions.setSubmitting(false);
    }
  };

  return (
    <Formik
      initialValues={initialValues}
      validate={validate}
      onSubmit={handleSubmit}
      validateOnChange={false}
      validateOnBlur={false}
    >
      {({ isSubmitting, submitForm }) => (
        <MultiPageFormContext.Provider
          value={{
            formScreen,
            isSubmitting,
            submitForm,
            flow,
            handleNextScreen,
            handlePrevScreen,
            activeScreen: screens[formScreen],
          }}
        >
          {children}
        </MultiPageFormContext.Provider>
      )}
    </Formik>
  );
};

export default memo(MultiPageForm);
