import React, { memo } from 'react';
import { Field } from 'formik';
import TextInput from '../inputs/TextInput';
import Pagination from '../shared/Pagination';
import Wrapper from './Wrapper';

const Social = () => (
  <Wrapper
    currentScreen={1}
    skipScreenText="Skip step"
    skipScreenRoute="/explore/profile-setup/bio"
    heading="Website & Social"
    subheading="Link to your website or social media to broaden your connections."
  >
    <Field name="website">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={form.values.visibleFields.website}
          label="Website"
          placeholder="Enter website url"
          error={form.errors.website}
          touched={form.touched.website}
          form={form}
        />
      )}
    </Field>
    <Field name="linkedIn">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={form.values.visibleFields.linkedIn}
          label="LinkedIn"
          placeholder="Add LinkedIn url"
          error={form.errors.linkedIn}
          touched={form.touched.linkedIn}
          form={form}
        />
      )}
    </Field>
    <Field name="instagram">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={form.values.visibleFields.instagram}
          label="Instagram"
          placeholder="Add Instagram url"
          error={form.errors.instagram}
          touched={form.touched.instagram}
          form={form}
        />
      )}
    </Field>
    <Field name="facebook">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={form.values.visibleFields.facebook}
          label="Facebook"
          placeholder="Add Facebook url"
          error={form.errors.facebook}
          touched={form.touched.facebook}
          form={form}
        />
      )}
    </Field>
    <Field name="twitter">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={form.values.visibleFields.twitter}
          label="Twitter"
          placeholder="Add Twitter url"
          error={form.errors.twitter}
          touched={form.touched.twitter}
          form={form}
        />
      )}
    </Field>
    <Pagination centered prevActive nextActive />
  </Wrapper>
);

export default memo(Social);
