import React, { memo } from 'react';
import styled from 'styled-components';
import { BodyText } from '../styled/Typography';
import Button from '../styled/Button';
import { fontFamilies, viewports } from '../../utils/variables';
import CloseButton from '../shared/CloseButton';

const Finish = ({ setActiveScreen, refetch }) => (
  <Base>
    <Heading>You’re good to go.</Heading>
    <BodyText>
      You can always access your account settings and profile settings to make
      updates or edits. Click the gear icon in the top right hand corner to
      access your settings, instructions on using the platform, or to log out.
    </BodyText>
    <Navigation>
      <WideButton
        secondary
        onClick={() => {
          refetch();
          setActiveScreen(undefined);
        }}
      >
        Skip the Tour
      </WideButton>
      <WideButton
        onClick={() => {
          refetch();
          setActiveScreen('tour');
        }}
      >
        Take the Tour
      </WideButton>
    </Navigation>
    <CloseLink onClick={() => setActiveScreen(undefined)} />
  </Base>
);

export default memo(Finish);

const Base = styled.div`
  padding: 5rem 0 3.75rem;
  position: relative;
  text-align: center;
`;

const Heading = styled.h2`
  font-size: 3rem;
  font-family: ${fontFamilies.bold};
  letter-spacing: -0.43px;
  line-height: 54px;
  margin-bottom: 1.25rem;
`;

const Navigation = styled.div`
  display: flex;
  justify-content: center;
`;

const WideButton = styled(Button)`
  margin: 0 0.625rem;
  width: 13.375rem;
`;

const CloseLink = styled(CloseButton)`
  position: fixed;
  right: 1.9375rem;
  top: 1.9375rem;

  @media ${viewports.medium} {
    right: -1.125rem;
    position: absolute;
    top: 1.9375rem;
  }
`;
