import React, { memo, useContext } from 'react';
import styled from 'styled-components';
import ProgressNav from '../shared/ProgressNav';
import { Heading, Subheading } from '../styled/Typography';
import { colors, viewports } from '../../utils/variables';
import { MultiPageFormContext } from '../shared/MultiPageForm';

const Wrapper = ({ children, isLast, heading, subheading, skipScreenText }) => {
  const { handleNextScreen, submitForm } = useContext(MultiPageFormContext);

  return (
    <Base>
      <ProgressNav numScreens={3} firstIndex={1} />
      {skipScreenText && (
        <SkipScreen onClick={isLast ? submitForm : handleNextScreen}>
          {skipScreenText}
        </SkipScreen>
      )}
      <Heading>
        <span>My Profile:</span> {heading}
      </Heading>
      <Subheading>{subheading}</Subheading>
      {children}
    </Base>
  );
};

export default memo(Wrapper);

const Base = styled.div`
  @media ${viewports.medium} {
    padding: 3.125rem 0;
  }
`;

const SkipScreen = styled.span`
  color: ${colors.blue};
  cursor: pointer;
  display: none;
  position: absolute;
  right: 3.125rem;
  top: 3.5rem;

  @media ${viewports.medium} {
    display: block;
  }
`;
