import React, { memo } from 'react';
import { Field } from 'formik';
import TextInput from '../inputs/TextInput';
import SelectInput from '../inputs/SelectInput';
import Pagination from '../shared/Pagination';
import Required from '../styled/Required';
import Wrapper from './Wrapper';
import { industries } from '../../utils/selectOptions';

const Career = () => (
  <Wrapper
    currentScreen={0}
    heading="Career Details"
    subheading="Enter your current career details. This information is required."
  >
    <Field name="currentCompany">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={form.values.visibleFields.currentCompany}
          form={form}
          label="Current Company*"
          placeholder="Enter company name"
          error={form.errors.currentCompany}
          touched={form.touched.currentCompany}
        />
      )}
    </Field>
    <Field name="currentJobTitle">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={form.values.visibleFields.currentJobTitle}
          form={form}
          label="Current Job Title*"
          placeholder="Select the best match"
          error={form.errors.currentJobTitle}
          touched={form.touched.currentJobTitle}
        />
      )}
    </Field>
    <Field name="currentIndustry">
      {({ field, form }) => (
        <SelectInput
          {...field}
          toggle
          toggled={form.values.visibleFields.currentIndustry}
          form={form}
          label="Current Industry*"
          placeholder="Select the best match"
          options={industries}
          error={form.errors.currentIndustry}
          touched={form.touched.currentIndustry}
        />
      )}
    </Field>
    <Required>*Required Fields</Required>
    <Pagination centered nextActive />
  </Wrapper>
);

export default memo(Career);
